body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.TodoHeader {
    background-color: rgb(23, 23, 216);
    color: white;
    border-radius: 5px 5px 0 0;
}

.TodoHeader>h2 {
    margin-bottom: 0;
}

.TodoHeader>p {
    padding-bottom: 3px;
    margin-top: 10px;
}

a {
    color: white;
    text-decoration: none;
}
.input {
    padding: 10px;
    border: 1px solid gray;
    font-size: 18px;
    width: 75%;
}

.input:hover {
    border: 1px solid rgb(157, 48, 48);
}
.todo {
    background: rgb(65, 80, 190);
    margin: 10px;
    padding: 10px;
    text-align: left;
    color: white;
    font-weight: 600;
    font-size: 18px;
    border-radius: 5px;
}

.buttons {
    float: right;
    display: flex;
}

.buttons>div {
    background: rgb(91, 137, 236);
    padding: 0 5px 5px 5px;
    cursor: pointer;
    border-radius: 5px;
}

.buttons>div:hover {
    background: rgb(60, 116, 235);
}

.complete {
    margin-right: 5px;
}
.App {
    text-align: center;
    margin-left: 35%;
    margin-right: 35%;
    box-shadow: 0 1px 3px rgb(117, 114, 114);
    padding: 0 0 1px 0;
    border-radius: 5px;
}
